import { Tooltip, Box, Button, Text, Menu, MenuButton, MenuList, Avatar, MenuDivider, MenuItem } from '@chakra-ui/react';
import React, { useState} from 'react';
import { BellIcon, ChevronDownIcon } from "@chakra-ui/icons";
import { ChatState } from '../../context/ChatProvider';
import ProfileModal from './ProfileModal';
import { useNavigate } from 'react-router-dom';
import { useDisclosure } from '@chakra-ui/hooks'
import axios from "axios";
import { useToast } from "@chakra-ui/toast";
import { Spinner } from "@chakra-ui/spinner";
import {
    Drawer,
    DrawerBody,
    DrawerFooter,
    DrawerHeader,
    DrawerOverlay,
    DrawerContent,
    DrawerCloseButton,
    Input,
} from '@chakra-ui/react'
import ChatLoading from '../ChatLoading';
import UserListItem from '../UserAvatar/UserListItem';
import { getSender } from '../../config/ChatLogic';
import NotificationBadge, { Effect } from 'react-notification-badge';


const SideDrawer = () => {

    const [ search, setSearch] = useState();
    const [ searchResult, setSearchResult] = useState();
    const [ loading, setLoading ] = useState();
    const [ loadingChat, setLoadingChat] = useState();
    const navigate = useNavigate();
    const toast = useToast();

    const {
        user,
        setSelectedChat,
        chats,
        setChats,
        notification,
        setNotification
    } = ChatState();

    const { isOpen, onOpen, onClose } = useDisclosure();


    const logoutHandler = () => {
    localStorage.removeItem("userInfo");
    navigate("/");
    };

    const handleSearch = async () => {
    if (!search) {
        toast({
        title: "Please Enter something in search",
        status: "warning",
        duration: 5000,
        isClosable: true,
        position: "top-left",
        });
        return;
    }

    try {
        setLoading(true);

        const config = {
        headers: {
            Authorization: `Bearer ${user.token}`,
        },
    };

    const { data } = await axios.get(`/api/user?search=${search}`, config);

        setLoading(false);
        setSearchResult(data);
    } catch (error) {
        toast({
            title: "Error Occured!",
            description: "Failed to Load the Search Results",
            status: "error",
            duration: 5000,
            isClosable: true,
            position: "bottom-left",
        });
    }
};

const accessChat = async (userId) => {
    console.log(userId);

    try {
        setLoadingChat(true);
        const config = {
        headers: {
            "Content-type": "application/json",
            Authorization: `Bearer ${user.token}`,
        },
    };
        const { data } = await axios.post(`/api/chat`, { userId }, config);
        if (!chats.find((c) => c._id === data._id)) setChats([data, ...chats]);
        setSelectedChat(data);
        setLoadingChat(false);
        onClose();
    } catch (error) {
        toast({
            title: "Error fetching the chat",
            description: error.message,
            status: "error",
            duration: 5000,
            isClosable: true,
            position: "bottom-left",
        });
    }
};


    return (
    <div>
        <Box
        justifyContent="space-between"
        alignItems="center"
        bg="white"
        w="100%"
        p="5px 10px 5px 10px"
        borderWidth="5px"
        display="flex"
        
        >
            <Tooltip label="Search users to chat" hasArrow placement='bottom-end'>

                <Button varient="ghost" onClick={onOpen}>
                    <i className="fa-solid fa-magnifying-glass"></i>
                    <Text display={{ base: "none", md: "flex"}} px="4px">
                        Search User
                    </Text>
                </Button>

            </Tooltip>
            <Text fontSize="3xl" fontFamily="'MuseoModerno', cursive">
                ChatterBox
            </Text>
            <div>
                <Menu>
                    <MenuButton p={1}>
                        <NotificationBadge
                        count={notification.length}
                        effect={Effect.SCALE}
                        />
                        <BellIcon fontSize="2xl" m={1}/>
                    </MenuButton>
                    <MenuList pl={2}>
                        {!notification.length && "No New Messages"}
                        {notification.map((notif) =>(
                            <MenuItem key={notif._id} onClick={() => {
                                setSelectedChat(notif.chat)
                                setNotification(notification.filter((n)=> n !== notif))
                            }}>
                                {notif.chat.isGroupChat ? `New Message in ${notif.chat.chatName}`
                                : `New message from ${getSender(user, notif.chat.users)}`}
                            </MenuItem>
                        ))}
                        </MenuList>
                </Menu>
                <Menu>
                    <MenuButton as={Button} rightIcon={<ChevronDownIcon/>}>
                        <Avatar size='sm' cursor='pointer' name={user.name} src={user.pic}/>
                    </MenuButton>
                    <MenuList>
                        <ProfileModal user={user}>
                            <MenuItem>My Profile</MenuItem>
                        </ProfileModal>
                            <MenuDivider></MenuDivider>
                            <MenuItem onClick={logoutHandler}>Logout</MenuItem>
                    </MenuList>
                </Menu>
            </div>
        </Box>
        <Drawer placement='left' onClose={onClose} isOpen={isOpen}>
            <DrawerOverlay />
            <DrawerContent>
                <DrawerHeader borderBottom="1px">Search Users</DrawerHeader>
                <DrawerBody>
                    <Box display="flex" pb={2}>
                        <Input
                            placeholder="Search by name or email"
                            mr={2}
                            value={search}
                            onChange={(e) => setSearch(e.target.value)}
                                />
                        <Button onClick={handleSearch}>Go</Button>
                    </Box>
                    {loading ? (
                        <ChatLoading/>
                    ) : (
                        searchResult?.map(user => (
                            <UserListItem
                                key={user._id}
                                user={user}
                                handleFunction={() => accessChat(user._id)}
                            />
                        ))
                    )}
                    {loadingChat && <Spinner ml="auto" display="flex"/>}
                </DrawerBody>
            </DrawerContent>
        </Drawer>
    </div>
    )
}

export default SideDrawer;
